@import "colors";

/**************************************************/
/******************* HEADER BAR *******************/
/**************************************************/

/* Active on messenger */
._2v6o {
  color: $foreground !important; }

/* Friend Name */
._17w2 {
  background-color: $background !important;
  color: $foreground !important; }



/**************************************************/
/****************** MESSAGES **********************/
/**************************************************/

/* Links, website URL in link preview */
a.__6m {
  color: $foreground !important; }


/* Message color */
span._3oh- {
  color: $foreground !important; }

/* Persone Name Header */
div._673w {
  background-color: $background; }

/* Message background from friend*/
._29_7 ._hh7 {
  background-color: $lightgray; }

/* Message background from me */
._3i_m._nd_ ._3058._ui9._hh7._s1-._52mr {
  background-color: $selection !important; }

/* New Messaging Person's Name */
._llq {
  color: $foreground !important; }

._36zg, ._1n-e {
  color: $foreground !important; }

// Group convo: user's name
._ih3 {
  color: $foreground; }

// Attachment unavailable: bubble
._5i_d {
  border: 1px solid $selection;
  background-color: transparent !important;
  // Attachment Unavailable
  .__6k {
    color: $foreground !important; }

  .__6l {
    color: $foreground !important; } }

/* Emojis in main messaging section */
._1ifu {
  padding: 10px; }

/* Emojis inside the emoji menu */
._5f0v._4pia ._1ifu {
  padding: 0 !important; }

/* BG */
._4_j4 {
  background-color: $background !important; }

/* Friend has called you text */
img, ._50f7 {
  color: $cyan; }



/**************************************************/
/**************** WRITE NEW MESSAGE ***************/
/**************************************************/

/* Enter New Message BG */
._4rv3 {
  background-color: $background !important; }


// Write a new message to a person
._2y8y._5l-3 {
  background: $background !important;
  ._2y8z {
    color: $foreground !important; }

  ._58al {
    color: $foreground !important;
    background-color: $background; } }



// Icon that use PNGs

._4rv6 {
  filter: invert(100%); }


a._4ce_ {
  filter: invert(100%); }


/* Enter New Msg PlaceHolder Text */
._5rpu {
  color: $foreground !important; }

